<template>
  <div id="home-list">
    <p>HomeListÁªÑ‰ª∂</p>
    <ul class="list">
      <v-touch
        v-for="(item, index) of pageList"
        :key="index"
        tag="li"
        @tap="clickItem(index)"
        @panright="rightMove(index)"
        @panleft="leftMove(index)"
        :class="['list-item',{'left-move':(index === current),'current-item':(index === ob)}]"
      >
      <!--
      ÂÖàÊ∏≤ÊüìÂá∫ÂàóË°®ÊâÄÊúâÊù°ÁõÆË°®ËææÂºè‰∏≠===Â∑¶ËæπÁöÑÁöÑindexÂÄºÔºå‰πãÂêéÊ†πÊçÆÂØπÂ∫îÊù°ÁõÆ‰∫ã‰ª∂ÁöÑÂèëÁîüÂÜçÊ¨°ÂàùÂßãÂåñcurrentÊàñob
      -->
      <!--
      <li @tap="clickItem(1)" @panright="rightMove(1)" :classs="{'current-item':(1 === ob)}"></li>
      <li @tap="clickItem(2)" @panright="rightMove(2)" :classs="{'current-item':(2 === ob)}"></li>
      -->
      <!--
      ÂΩìob‰∏∫2Êó∂ÔºåÂè™ÊúâÁ¨¨‰∫åÈ°πÁöÑcurrent-item classÁîüÊïà
      -->
        <div class="list-item-left">
          <p>{{item.note_title}}</p>
          <p>
            <span class="note-time">{{item.note_time}}</span>
            <span class="more-content" @click="updateItem(index)">&raquo;</span>
          </p>
        </div>
        <div class="list-item-right">
          <span
            @click="deleteItem(index)"
          >Âà†Èô§</span>
        </div>
      </v-touch>
    </ul>
    <p class="add-list-item">
    <svg @click="addItem" xmlns="http://www.w3.org/2000/svg" class="si-glyph-pencil" viewBox="0 0 17 16">
      <title>992</title>
      <defs></defs>
      <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
        <path d="M16.479,2.536 L14.474,0.546 C13.935,0.00899999997 13.091,-0.019 12.59,0.482 L3.47,9.616 C3.47,9.616 1.158,14.936 1.001,15.366 C0.876,15.706 1.307,16.137 1.605,15.991 C2.085,15.754 7.345,13.471 7.345,13.471 L16.487,4.299 C16.989,3.797 17.018,3.073 16.479,2.536 L16.479,2.536 Z M2.312,13.906 L4.033,10.279 L4.015,10.985 L5.013,10.971 L14.537,1.529 L15.796,2.765 L6.612,11.73 L6.385,12.482 L6.871,12.968 L3.06,14.624 L2.312,13.906 L2.312,13.906 Z" fill="#434343" class="si-glyph-fill"></path>
      </g>
    </svg>
    </p>
  </div>
</template>

<script type="text/javascript">
import store from '@/store/store'
import Menu from '@/components/menu.js'
export default {
  name: 'HomeListÁªÑ‰ª∂',
  data () {
    return {
      ob: Number,
      current: -1,
      last: null,
      oo: false,
      leftMark: false
    }
  },
  created () {
    Menu.$emit('mark', 1) // Â∞ÜÂΩìÂâçÈ°µÈù¢markÊ†áËÆ∞‰∏∫1ÔºåÈÄöËøá‰∫ã‰ª∂ÊÄªÁ∫øMenuÔºåÂáÜÂ§á‰∏éÂÖÑÂºüÁªÑ‰ª∂HomeËøõË°åÈÄö‰ø°
  },
  computed: {
    pageList () {
      return store.state.list
    }
  },
  methods: {
    clickItem (value) {
      this.current = -1 // ÁÇπÂáªËÅöÁÑ¶ÂâçÔºåÂÖàÈáçÁΩÆÂ∑¶Âè≥ÊªëÂä®‰∫ã‰ª∂
      this.ob = value
    },
    updateItem (value) {
      store.state.click_item_num = value // Â∞ÜÁÇπÂáªÊù°ÁõÆÁºñÂè∑Â≠òÂÖ•storeÔºå‰∏∫updateÁªÑ‰ª∂ÈíàÂØπÊÄßÊèêÂèñ‰ø°ÊÅØÂÅöÂáÜÂ§á
      this.$router.push('/update')
    },
    deleteItem (value) {
      var askDelete = window.confirm('Âà†Èô§ËØ•Êù°ÁõÆÔºü')
      if (askDelete) {
        store.state.list.splice(value, 1)
        alert('Â∑≤Âà†Èô§ËØ•Êù°ÁõÆ')
        this.current = -1
      }
      console.log(store.state.list)
    },
    leftMove (value) {
      this.ob = value // ÊâßË°å‰∏ãÊñπÊìç‰ΩúÂâçÂÖàËÅöÁÑ¶
      if (this.last === value) { // Ëã•ËøûÁª≠‰∏§Ê¨°ÂùáÂ∑¶ÊªëÂêå‰∏ÄÂØπË±°
        this.oo = !this.oo // ÈÄöËøáÂèñÂèçÔºå2Ê¨°‰∏≠ÂøÖÊúâ1Ê¨°ÈáçÁΩÆcurrentÔºà1Â∑¶Êªë-2Â∑¶ÊªëÂΩí‰Ωç-3Â∑¶ÊªëÔºâÔºÅ
        if (this.oo) { // Áî±‰∫éÊòØpanleft,panright‰∫ã‰ª∂ÔºåËØ•Êù°‰ª∂‰∏ã‰∏çÈúÄË¶Å‰ªª‰ΩïÊìç‰Ωú
          // this.current = -1 // ÈáçÁΩÆcurrent‰∏∫-1
          return false
        }
        this.current = value
        this.last = value // ÊöÇÂ≠òÊ≠§Ê¨°Â∑¶ÊªëÂØπË±°ÁöÑÁºñÂè∑‰∫élast
        this.leftMark = true // ‰∏ÄÊó¶ÂØπË±°Â∑¶ÊªëÔºåËæπÊ†áËÆ∞ÂÖ∂Áä∂ÊÄÅ leftMark ‰∏∫trueÔºå‰∏∫ÈöèÂêé
      } else { // Ëã•ËøûÁª≠‰∏§Ê¨°Â∑¶Êªë‰∏çÂêåÂØπË±°Ôºå‰ºöËá™Âä®ÈáçÁΩÆ‰πãÂâçÁöÑÊìç‰ΩúÂØπË±°ÔºÅ
        this.current = value
        this.last = value
        this.leftMark = true
      }
    },
    rightMove (value) {
      this.ob = value // ÊâßË°å‰∏ãÊñπÊìç‰ΩúÂâçÂÖàËÅöÁÑ¶
      if (this.last === value && this.leftMark === true) { // Ëã•ËøûÁª≠‰∏§Ê¨°ÂùáÂ∑¶ÊªëÂêå‰∏ÄÂØπË±°,‰∏î‰∏ä‰∏ÄÊ¨°ÊòØÂ∑¶ÊªëÊìç‰Ωú
        this.current = -1 // ÈáçÁΩÆcurrent‰∏∫-1
      }
    },
    addItem () {
      this.$router.push('/add')
    }
  }
}
</script>

<style type="text/css" scoped>
#home-list {
  border: 1px solid green
}
.list {
  text-align: center;
}
.list-item {
  position: relative;
  display: inline-block;
  width: 92%;
  padding: 10px 8px;
  background: #e1caca;
  margin-bottom: 6px;
  border-radius: 10px;
  text-align: left;
  font: 16px/32px 'arial';
  transition: transform .2s linear;
  touch-action: pan-y !important; /* Ëß£ÂÜ≥v-touch‰∏≠‰ΩøÁî®Â∑¶Âè≥ÊªëÂä®ÂΩ±Âìç‰∏ä‰∏ãÊªëÂä®bug */
}
.left-move {
  transform: translateX(-12%);
}
.more-content, .note-time {
  font: 14px/20px Ê•∑‰Ωì,arial;
  color: blue;
}
.more-content {
  line-height: 2px;
  float: right;
}
.current-item {
  background: #4eb2df;
}
.current-item .list-item-left {
  color: #fff;
}
.current-item .more-content, .current-item .note-time {
  color: #fff;
}
.list-item-right {
  position: absolute;
  top: 0;
  right: -13%;
  height: 100%;
  width: 12%;
  text-align: center;
  line-height: 84px;
  font-weight: 600;
  color: #a58888;
}
.add-list-item {
  display: inline-block;
  position: fixed;
  left: 50%;
  bottom: 10%;
  width: 30%;
  transform: translateX(-50%);
  border-radius: 5px;
}
svg {
  width: 45px;
  height: 45px;
  margin: 10px;
}
</style>



