import Vue from 'vue'
import Router from 'vue-router'
import store from '@/store/store'
import Login from '@/components/Login'
import Home from '@/components/Home'
import Add from '@/components/Add'
import Update from '@/components/Update'
import HomeList from '@/components/HomeList'
import HomeUser from '@/components/HomeUser'
import About from '@/components/About'
import BusTest from '@/components/BusTest'
import NoFound from '@/components/NoFound'

Vue.use(Router)

const router = new Router({
  // mode: 'history',
  routes: [{
    path: '/',
    component: Login,
    meta: {requiresAuth: true} // Ê∑ªÂä†Ë°®Á§∫ÈúÄË¶ÅÈ™åËØÅ
  }, {
    path: '/home', // Ë∑ØÁî±‰∏çÂå∫ÂàÜÂ§ßÂ∞èÂÜô
    // redirect: '/home/list',
    component: Home,
    meta: {requiresAuth: true},
    children: [{
      path: 'list', // ÈúÄÈ™åËØÅÔºåÁà∂Ë∑ØÁî±Â∑≤Ê∑ªÂä†requiresAuthÔºåËøôÈáå‰∏çÈúÄÊ∑ªÂä†
      component: HomeList
    }, {
      path: 'user',
      name: 'homeuser',
      component: HomeUser
    }]
  }, {
    path: '/about',
    name: 'ab', // router-viewÊ∏≤ÊüìÊó∂ÔºåÊåáÂÆöÁöÑÊ∏≤Êüì‰ΩçÁΩÆ
    component: About
  }, {
    path: '/bus',
    component: BusTest
  }, {
    path: '/add',
    component: Add,
    meta: {requiresAuth: true} // Ê∑ªÂä†Ë°®Á§∫ÈúÄË¶ÅÈ™åËØÅ
  }, {
    path: '/update',
    component: Update,
    meta: {requiresAuth: true} // Ê∑ªÂä†Ë°®Á§∫ÈúÄË¶ÅÈ™åËØÅ
  }, {
    path: '/404',
    name: 'notFound',
    component: NoFound
  }, {
    path: '*', // Ê≠§Â§ÑÈúÄÁâπÂà´Ê≥®ÊÑèÁΩÆ‰∫éÊúÄÂ∫ïÈÉ®
    redirect: '/404'
  }]
})
router.beforeEach((to, from, next) => { // Ë∑ØÁî±‰øùÂç´ÁõëÊéß ÁôªÂΩïÁä∂ÊÄÅ
  console.log(to.matched)
  if (to.matched.some(record => record.meta.requiresAuth)) { // ÈúÄË¶ÅÈ™åËØÅÁôªÂΩïÁä∂ÊÄÅÁöÑÈ°µÈù¢Ë∑ØÁî±
    // ËøôÈáåÂà§Êñ≠Áî®Êà∑ÊòØÂê¶ÁôªÂΩïÔºåÈ™åËØÅÊú¨Âú∞Â≠òÂÇ®ÊòØÂê¶Êúâtoken
    if (!store.state.status && to.path !== '/') { // Êú™ÁôªÂΩï,ÂéüË∑ØÁî±Âç¥ÊåáÂêëÈùûÁôªÂΩïÈ°µ
      next('/') // ÈáçÂÆöÂêëÂà∞ÁôªÂΩïÈ°µ
      console.log('11' + store.state.status)
    } else if (store.state.status && (to.path === '/')) { // Â∑≤ÁôªÂΩï,ÂéüË∑ØÁî±Âç¥ÊåáÂêëÁôªÂΩïÈ°µ
      next('/home/list') // ÈáçÂÆöÂêëÂà∞‰∏ªÈ°µ
      console.log('22' + store.state.status + to.path)
    } else {
      console.log('33' + store.state.status + to.path)
      next() // „ÄêÂøÖÈ°ªÊúâÔºÅÔºÅÔºÅÂê¶ÂàôÂΩì‰∏çÊª°Ë∂≥‰ª•‰∏ä2Êù°‰ª∂ÔºåÈ°µÈù¢Â∞ÜÊó†Ê≥ïËøõË°åËΩ¨Âêë„Äë
    }
  } else {
    console.log('44')
    next() // Á°Æ‰øù‰∏ÄÂÆöË¶ÅË∞ÉÁî® next()
  }
})
export default router



