// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import store from './store/store'
import router from './router'
import './assets/styles/reset.css'
import './assets/styles/border.css'
import App from './App'

Vue.config.productionTip = false

// Ê≥®ÂÜådoubletap‰∫ã‰ª∂
var VueTouch = require('vue-touch')
VueTouch.registerCustomEvent('doubletap', {
  type: 'tap',
  taps: 2
})

Vue.use(VueTouch, {name: 'v-touch'})

// È°µÈù¢Á¶ÅÊ≠¢Áî®Êà∑Áº©Êîæ
window.onload = function () {
  document.addEventListener('touchstart', function (event) {
    if (event.touches.length > 1) {
      event.preventDefault()
    }
  })
  document.addEventListener('gesturestart', function (event) {
    event.preventDefault()
  })
}

// ÊØèÊ¨°ÂàáÊç¢ÁªÑ‰ª∂ÊàñÂà∑Êñ∞Âùá‰ºöÊâßË°å(ÊåÇËΩΩvueÂÆû‰æã‰πãÂâçÂ∞±Ë¶ÅÊâßË°åÔºÅÔºÅ)
if (window.Storage && window.localStorage && window.localStorage instanceof Storage) {
  // Âú®È°µÈù¢Âä†ËΩΩÊó∂ËØªÂèñlocalStorage
  if (localStorage.getItem('store')) {
    store.replaceState(Object.assign({}, store.state, JSON.parse(localStorage.getItem('store'))))
  }
  setInterval(() => { // ÊØè10sÊääÁä∂ÊÄÅ‰øùÂ≠òÂà∞localStorage‰∏ÄÊ¨°
    localStorage.setItem('store', JSON.stringify(store.state))
    console.log('saved')
  }, 10000)
  // Âú®È°µÈù¢Âà∑Êñ∞Êó∂Â∞Üstore‰øùÂ≠òÂà∞localStorageÈáå(safariË≤å‰ºº‰∏çÊîØÊåÅbeforeunload)
  window.addEventListener('beforeunload', () => {
    localStorage.setItem('store', JSON.stringify(store.state))
  })
} else {
  alert('ÊÇ®ÁöÑÊµèËßàÂô® ‰∏çÊîØÊåÅlocalStorageÔºåÂ∞Ü‰ºöÂΩ±Âìç‰ΩøÁî®ÔºÅ')
}

/* eslint-disable no-new */
new Vue({ // ‰ΩøÁî®vue-routerÊó∂ÔºåË¶ÅÊúÄÂêéÊåÇËΩΩAppÊ†πÂÆû‰æãÔºå‰∏çÁÑ∂‰ºöÂØºËá¥ÈÖçÁΩÆ‰∏çÊàêÂäü!
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



